.image-submit-btn {
  background: #17252A;
  color: #FEFFFF;
  border: none;
  outline: none;
}

.image-url-field {
  outline: none;
  border: none;
}

.desc {
  color: #17252A;
}

#inputImage {
  padding: 3px;
  background: #DEF2F1;
  border-radius: 5px;
  margin-bottom: 10px;
}

.boundingBox {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

#modal-root {
  position: relative;
  z-index: 999;
}

.profile-modal {
  background: rgba(255, 255, 255, 0.8);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-container {
  background: #FEFFFF;
}

.modal-close {
  font-size: 3rem;
  cursor: pointer;
}

.modal-close:hover {
  color: grey;
  cursor: pointer;
}

.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
@media screen and (max-width: 600px) {
  .App {
    margin-left: 10px;
    margin-right: 10px;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #3AAFA9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  cursor: pointer;
  border: none;
}

